taticClass:"my-7 text-sm tracking-wider leading-8 lg:leading-7"},[_vm._v(" 当社で働くにあたって、その根底にあるのは企業理念の実践です。企業理念にある、「お客様にとって、なくてはならない存在。それが私たちの誇りです」、「誰よりもお客様の近くで、誰よりも深くお客様を想い、誰よりも情熱をもってお客様の力になります」、そして「お客様とともに未来へ向かって成長し続けます」というビジョン。誰のために、何のために、どのような姿勢で働くのか。私たちを支えてくださっている方々への感謝の気持ちを忘れず、仕事に臨むことが大切です。そしてこの企業理念の実践をベースとした上で、変化の激しい社会環境にいかに機敏に対応していくかも求められます。そのためには、市場および事業全体を俯瞰し、柔軟な発想と広い視野、高い視座で計画を立案・実行し、結果を導き出すこと。そうした企業理念を実践しつつも、「既成概念に捉われない、変革志向と柔軟な発想を持つ人」、「主体的にアイデアを創出・具現化し、結果を出すことができる人」との出会いを楽しみにしています。 ")])]),_c('section',{staticClass:"py-8"},[_c('h3',{staticClass:"section-sub-title text-lg md:text-2xl pt-5 tracking-wider bold",staticStyle:{"color":"#333333"}},[_vm._v(" 人事部長として心がけていること ")]),_c('hr',{staticClass:"border-2 mt-2 mb-4"}),_c('p',{staticClass:"\n          NotoSansJapanesee\n          bg-white\n          mt-12\n          p-5\n          font-semibold\n          tracking-widest\n          text-gray-700 text-lg\n          md:text-2xl\n        "},[_vm._v(" 「人材」を「人財」へと"),_c('br',{staticClass:"sp-br"}),_vm._v("進化させていくために ")]),_c('p',{staticClass:"my-7 text-sm tracking-wider leading-8 lg:leading-7"},[_vm._v(" 普段から心がけているのは、「否定をしないこと」と「メンバーの様子に気を配ること」。共に働く仲間とその一人ひとりが持つ熱意を大切にしたい......。人事部長として、それぞれの個性を活かし、尊重する人材育成を行っていきたいと考えています。組織を城の城壁に例えると、城壁は様々な形と大きさの石垣が重なり合い、崩れない強固な土台を築いています。同様に、企業も一人ひとりの個性や強みが組み合わさり、相乗効果が生まれることで強い組織が形成されます。「人材」が貴重な「人財」に進化するよう、制度面・環境面双方の向上に注力し、東急百貨店の今と未来を支えていくことが自分の役目であると考えています。 ")])]),_c('section',{staticClass:"pt-8 pb-24"},[_c('h3',{staticClass:"section-sub-title text-lg md:text-2xl pt-5 tracking-wider bold",staticStyle:{"color":"#333333"}},[_vm._v(" 応募者へのメッセージ ")]),_c('hr',{staticClass:"border-2 mt-2 mb-4"}),_c('p',{staticClass:"\n          NotoSansJapanesee\n          bg-white\n          mt-12\n          p-5\n          font-semibold\n          tracking-widest\n          text-gray-700 text-lg\n          md:text-2xl\n        "},[_vm._v(" 共に歩み、成長する、"),_c('br',{staticClass:"sp-br"}),_vm._v("未来へ向かって ")]),_c('p',{staticClass:"my-7 text-sm tracking-wider lead